// ModalReducer.js
const initialState = {
  modalIsOpen: false, //    
  modalContent: '', //    
};

const modalReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'OPEN_MODAL': //  
      return {
        ...state,
        modalIsOpen: true,
        modalContent: action.content,
      };
    case 'CLOSE_MODAL': //  
      return {
        ...state,
        modalIsOpen: false,
        modalContent: '',
      };
    default:
      return state;
  }
};

export default modalReducer;
