// FormReducer.js
const initialState = {
  username: '', //  
  password: '', // 
  //     
};

const formReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'UPDATE_FIELD':
      return {
        ...state,
        [action.fieldName]: action.fieldValue,
      };
    case 'RESET_FORM':
      return initialState;
    default:
      return state;
  }
};

export default formReducer;
